import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';

import { AppComponent } from './app.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { HeaderComponent } from './dashboard/header/header.component';
import { LeftNavComponent } from './dashboard/left-nav/left-nav.component';
import { JobRecordViewComponent } from './dashboard/job-record-view/job-record-view.component';
import {LeftNavService} from "./shared/service/left-nav.service";


@NgModule({
  declarations: [
    AppComponent,
    DashboardComponent,
    HeaderComponent,
    LeftNavComponent,
    JobRecordViewComponent
  ],
  imports: [
    BrowserModule,
    NgbModule.forRoot()
  ],
  providers: [
    NgbModule,
    LeftNavService,
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



